  created() {
    this.icon =
      this.iconType == "Filled"
        ? this.filledIcon
        : this.iconType == "Outhline"
        ? this.outhlineIcon
        : this.allIcon;
  },
  methods: {
    iconTypeChange(Radio) {
      if (this.iconType == "Filled") {
        this.icon = this.filledIcon;
      }
      if (this.iconType == "Outhline") {
        this.icon = this.outhlineIcon;
      }
    },
    searchIconVal: debounce(function () {
      if (typeof this.searchText == "string" && this.searchText.length > 0) {
        // 需要做查询的字符串，去空格
        const srt = this.searchText.replace(/\s/g, "");
        // 改造字符串
        const reg = new RegExp(srt, "i");
        if (this.iconType == "Filled") {
          this.icon = this.filledIcon.filter((item) => {
            return reg.test(item);
          });
        }
        if (this.iconType == "Outhline") {
          this.icon = this.outhlineIcon.filter((item) => {
            return reg.test(item);
          });
        }
      } else {
        this.iconTypeChange();
      }
    }, 500),
    copeIcon(icon) {
      let oInput = document.createElement("input");
      oInput.value = icon;
      document.body.appendChild(oInput);
      oInput.select(); // 选择对象;
      document.execCommand("Copy");
      oInput.remove();
      this.$Message.success("复制成功");
    },
  },
};
